<template>
    <div class="home-page">
        
        <div class="common-top-nav">
            <img src="@/assets/images/airdrop.png" alt="">
            <span class="tit">邀请新用户领空投</span>
        </div>
        <div class="content">
            <div class="link-address" v-if="address">
                <div class="link">
                    {{reAddress}}
                </div>
                <div class="copy-outer">
                    <div class="copy" v-clipboard:copy="reAddress" v-clipboard:success="copySuccess">
                        复制邀请链接
                    </div>
                </div>
            </div>
            <div class="rules">
                <p class="tit">邀请奖励规则：</p>
                <p> <span>1、</span> 新用户必须使用邀请人的邀请链接进入网站，并领取空投才视为邀请成功，</p>
                <p> <span>2、</span> 新用户受邀后，邀请人与受邀者同时获得一份空投奖励，</p>
                <p> <span>3、</span> 受邀人作为新的邀请人，再次邀请新用户成功后受邀人的邀请人也获得一份空投，依次类推最大邀请层级为四级，</p>
                <p> <span>4、</span> 邀请人关系成立后，邀请双方不仅会获得空投奖励，同时会获得下级挖矿收益一定比例奖励，详细奖励参官方考奖励公告,</p>
                <p> <span>5、</span> 所有人挖矿收益的一定比例将会平均分配给上级，没有上级而未分配部分将进行销毁，</p>
                <p> <span>6、</span> 挖矿推荐奖励的发放，要求被奖励的账户的锁仓金额达到被推荐人锁仓金额的10%，如果没达到，也直接销毁。</p>
            </div>
            <div class="common-content-title">
                <span class="tit">我的空投奖励</span>
                <span class="tips">(受邀用户成功领取空投才能视为邀请成功)</span>
            </div>
            <div class="receive-area">
                <div class="kind">
                    <img src="@/assets/images/bnb_token.png" >
                    <span>H2O</span>
                </div>
                <div class="line">
                    <div class="tit">
                        待领取
                    </div>
                    <div class="cnt">
                        $ {{isTokenBtnDisbaled ? '1' :  airdropBalance }}
                    </div>
                </div>
                <div class="line">
                    <div class="tit">
                        已领取
                    </div>
                    <div class="cnt">
                        $ {{airDropDrawed}}
                    </div>
                </div>
                <div class="operation">
                    <div 
                        @click="receiveDrop"
                        :class="{'disabled':(!isTokenBtnDisbaled && airdropBalance === '0') || !isConnected}">领取</div>
                </div>
            </div>
            <div class="common-content-title">
                <span class="tit">邀请详情</span>
                <span class="tips">(一级)</span>
            </div>
            <div class="reward">
               
                <div class="list-outer" v-if="!inviteListState">
                    <div
                        v-for="(item,index) in inviteList" 
                        :key="index"
                        class="item">
                        {{item}}
                    </div>
                    
                </div>
                <div :class="['loading']" v-else >
                    {{inviteListText}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex';
import { receiveAirdrop } from '@/wallet/trade'
import { Toast } from 'mint-ui';
export default {
    name:'home',
    data(){
        return {
        }
    },
    mounted(){
        console.log('query' , this.$route.query);
    },
    computed:{
        ...mapState({
            address:state=>state.base.address,
            isConnected:state=>state.base.isConnected,
            listLoading:state=>state.base.inviteList.loading, 
            inviteList:state=>state.base.inviteList.list,
            airdropBalance:state=>state.base.airdropBalance,
            airDropDrawed:state=>state.base.airDropDrawed
        }),
        
        isTokenBtnDisbaled(){
            return this.airDropDrawed === '0' && this.airdropBalance === '0'
        },
        reAddress(){
            return `http://47.241.171.176/index.html#/airdrop?re=${this.address}`
        },
        inviteListState(){
            return !this.isConnected || this.listLoading || !this.inviteList.length
        },
        inviteListText(){
            let text = ''
            if(this.isConnected){
                if(this.listLoading ){
                    text = '加载中...'
                }else {
                    if(!this.inviteList.length){
                        text = '暂无邀请详情'
                    }
                }
            }else {
                text = '请先链接钱包'
            }
            return text
        },
        recommandAddress(){
            const { query } = this.$route
            return query.re
        },
    },
    methods:{
        copySuccess(){
            Toast({
                message: '复制成功',
                duration:2000
            });
        },
        async receiveDrop(){
            if(!this.isTokenBtnDisbaled && this.airdropBalance === '0') return 
            let address = this.recommandAddress
            if(!address ){
                address = this.address
            }
            console.log('邀请地址' , address);
            receiveAirdrop(address)
        },
    }
}
</script>
<style lang="scss" scoped>
.home-page {
    background-color: #fff;
    min-height: calc(100vh - 1.78rem);
    border-radius: .3rem;
}
.content {
    padding: .3rem;
    .link-address {
        .link {
            line-height: .4rem;
            padding: .22rem .3rem;
            word-break: break-all;
            color: #333333;
            background-color: rgba(0, 48, 255 , 0.06);
            border-radius: .1rem;
        }
        .copy-outer {
            display: flex;
            justify-content: flex-end;
            padding: .2rem 0 .4rem 0;
            .copy {
                width: 2.5rem;
                height: .5rem;
                border-radius: .25rem;
                border: 1px solid #002EFF;
                color: #002EFF;
                line-height: .5rem;
                text-align: center;
                font-size: .26rem;
            }
        }
    }
    .rules {
        padding: .3rem;
        background-color: rgba(0, 0, 0 , .06);
        border-radius: .1rem;
        line-height: .56rem;
        p{
            margin: 0;
            position: relative;
        }
        .tit {
            font-size: .28rem;
            font-weight: bold;
            line-height: .28rem;
            margin-bottom: .1rem;
        }
        p:not(.tit) {
            padding-left: .4rem;
            span {
                position: absolute;
                left: 0;
                top: 0;
            }
        }
    }
    .receive-area {
        padding: .3rem;
        background-color: #fff;
        background-color: rgba(0, 0, 0 , .06);
        border-radius: .1rem;
        .kind {
            font-size: .26rem;
            img{
                height: .46rem;
                vertical-align: middle;
                position: relative;
                top: -.03rem;
                margin-right: .1rem;
            }
        }
        .line {
            display: flex;
            justify-content: space-between;
            padding-left: .1rem;
            color: #666666;
            margin-top: .24rem;
        }
        .operation {
            padding: .6rem 0 .3rem 0;
            >div {
                width: 1.5rem;
                height: .5rem;
                border: 1px solid #0024FF;
                color: #0024FF;
                text-align: center;
                line-height: .5rem;
                font-size: .26rem;
                border-radius: .25rem;
                margin: 0 auto;
            }
            .disabled {
                color: #666666;
                border-color: #666666;
            }
        }
        
    }
    .reward {
        .list-outer {
            .item {
                padding-left: .3rem;
                height: .8rem;
                line-height: .8rem;
                background-color: rgba(0, 48, 255 , .06);
                border-radius: .1rem;
                color: #333333;
                margin-top: .2rem;
            }
            .item:nth-of-type(1){
                margin-top: 0;
            }
            
        }
        .loading {
            height: 1rem;
            line-height: 1rem;
            text-align: center; 
            font-size: .3rem;
            font-weight: 600;
            background-color: rgba(0,0,0,0.06);
            border-radius: .1rem;
        }
    }
}
</style>


